.pizza-filter {
    position: relative;
}

.pizza-filter__btn {
    font-weight: 700;
    font-size: 1rem;
    font-family: 'Alegreya';
    text-transform: uppercase;
}

.pizza-filter__btn:hover {
    color: red;
}

.pizza-filter__img {
    position: absolute;
    left:50%;
    bottom: 0;
    opacity: 0;
    visibility: hidden;
    transform:translate(-50%, 0%);
    transition: all .5s cubic-bezier(0.68, -0.55, 0.265, 1.55);

}

.pizza-filter:hover .pizza-filter__img {
    bottom: 30px;
    opacity: 1;
    visibility: visible;
}